import $ from 'jquery';

const subscribeForm = document.getElementsByClassName('form_subscribe')[0];

$('.form_subscribe').submit(function (event) {
	console.log(encodeURIComponent(subscribeForm.elements.email.value));
	const $form = $(this);
	$.ajax({
		type: 'POST',
		url: '/subscribe',
		data: $form.serialize()
	});
	event.preventDefault();
});



